@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100vh;
  background: url("https://4kwallpapers.com/images/wallpapers/macos-big-sur-apple-layers-fluidic-colorful-dark-wwdc-2020-5120x2880-1432.jpg");
  background-size: cover;
  background-attachment: fixed;

  font-family: "Poppins", sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  width: 40%;
  margin: auto;

  padding: 20px 20px;
  color: #198754;
  text-align: center;
}

.btn-convert {
  display: flex;
  justify-content: center;
  font-weight: 800;
}

.C2F_container {
  padding: 10px 10px;
  margin-top: 30%;
  transform: scale(1.1);
  padding: 40px;
  background: rgba(0, 0, 0, 0.15);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.18);
  border-radius: 20px;
  border-left: 2px solid rgba(255, 255, 255, 0.18);
  border-top: 2px solid rgba(255, 255, 255, 0.18);
  color: white;
}
.C2F_container div {
  margin: 20px 0;
}
.labelTo {
  margin-left: 15%;
}
.C2F_container input {
  width: 40%;
  height: 30px;
  margin-right: 10px;
  color: white;
  border-radius: 10px;
  margin-left: 10px;
  text-align: center;
  background: rgba(0, 0, 0, 0.05);
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
  font-family: "Poppins", sans-seri;
  font-size: 17px;
}
.C2F_container input:focus {
  outline: none;
}
.C2F_container select {
  border-radius: 10px;
  height: 35px;
  font-weight: 600;
  padding: 10px 0.5px;
}
.C2F_container select:focus {
  outline: none;
}
.btn-convert div {
  margin-right: 20px;
  padding: 5px 10px;
  background-color: dodgerblue;
  color: white;

  border-radius: 10px;
  cursor: pointer;
}
.btn-convert :first-child {
  margin-left: 25%;
}
.btn-convert div:hover {
  opacity: 0.7;
}

